import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { NgbModal,NgbModule, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { AppComponent } from './app.component';
import { FdmadComponent } from './fdmad/fdmad.component';
import { RepassageComponent } from './repassage/repassage.component';
import { DemandeDevisComponent } from './demande-devis/demande-devis.component';

const appRoutes: Routes = [
  { path: 'fdmad', component: FdmadComponent },
  { path: 'repassage', component: RepassageComponent },
  
  { path: '**', component: FdmadComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    FdmadComponent,
    RepassageComponent,
    DemandeDevisComponent
  ],
  imports: [
    BrowserModule,
    NgbModule.forRoot(),    
    RouterModule.forRoot(appRoutes)
  ],
  providers: [NgbModal],
  bootstrap: [AppComponent]
})
export class AppModule { }



