import { Component, OnInit , Input, Output, EventEmitter} from '@angular/core';
import { EmailSyntax } from 'email-syntax';

@Component({
  selector: 'app-demande-devis',
  templateUrl: './demande-devis.component.html',
  styleUrls: ['./demande-devis.component.css']
})
export class DemandeDevisComponent implements OnInit {

  @Output() closeModal : EventEmitter<string> = new EventEmitter()
  
  constructor() { }

  ngOnInit() {
  }
  close(){
    this.closeModal.emit('bye');
  }
  send(name, email, subject, description, alert){
    if(name.value == "" || subject.value == "" || description.value == ""){
      
      this.alertShow(alert,"Tous les champs sont obligatoire");
    } 
    else if(!EmailSyntax.validate(email.value)){
      this.alertShow(alert,"E-mail incorrect");
    }else{
      this.closeModal.emit('bye');
    }
    
  }

  private alertShow(alert,msg){
    alert.style.visibility = 'visible'
    alert.innerHTML = msg
    setTimeout(function(){alert.style.visibility = 'hidden'},8000);
  }
}



